.Toolbar {
    height: 8vh;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #1a1a1a;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 150px;
    box-sizing: border-box;
    z-index: 90;
    box-shadow: 2px 3px 5px rgba(236, 235, 235, 0.3);
}
.Toolbar nav {
    height: 100%;
}

.Logo {
    height: 80%;
}

@media (max-width: 499px) {
    .Toolbar {
        padding: 0 20px;
    }
    .DesktopOnly {
        display: none;
    }
    .logoCenter {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
}